<template>
  <div id="topAnchor" class="index">
    <!--主页导航-->
    <Navigation :logo="logo" :PRIFIX="PRIFIX" @reload="reload" :navNum="navNum" :navigation="navigation"
                @navSearch="navSearch" :index="idx"></Navigation>
    <router-view :slide="slide" :PRIFIX="PRIFIX" :userInfo="userInfo" :reload="reload" :idx="idx"
                 :searchVal="searchVal" :dynamicUrl="dynamicUrl"
                 :recommendList="recommendList" :articleRecommendAgen="articleRecommendAgen"></router-view>

  </div>
</template>

<script type="text/ecmascript-6">
  import Navigation from "./navigation/navigation.vue"
  import NavigationBC from "./navigation/navigationBC.vue"
  import Content from "./index/index.vue"
  import ImgText from "./article/imgText/imgText.vue"
  import Imgs from "./article/imgs/imgs.vue"
  import Login from "./login.vue"
  import Search from "./search/search.vue"
  import SortPage from "./sortPage/sortPage.vue"
  import Videos from "./article/video/video.vue"
  import PersonInfo from "./personInfo/personInfo.vue"
  import UpdatePersonInfo from "./personInfo/updatePersonInfo.vue"
  import about from "../views/about/about.vue"
  import {readLocal,saveNav,saveNavCss,loadNavCss,loadNav} from "../../src/common/js/store.js"

  export default {
    props: {
      /*用户信息*/
      userInfo: Object,
    },
    data () {
      return {
        idx: 0,
        searchVal: '',
        /*PRIFIX*/
        PRIFIX: "https://news.immnc.com/",
        dynamicUrl: "https://p0.cdrysj.com/po",
        /*logo图标*/
        logo: {
          /*没有错误就返回0*/
          code: 0,
          message: "处理成功",
          data: "src/assets/img/logo.jpg"
        },
        /*导航*/
        navigation: [],
        navNum: 0,
        /*轮播图*/
        slide: [],
        /*推荐*/
        recommendList: {},
        articleRecommendAgen: []
      }
    },
    methods: {
      reload(name) {
        this.idx = name
      },
      navSearch(val) {
        this.searchVal = val
      }
    },
    components: {
      Navigation,
      Content,
      ImgText,
      NavigationBC,
      Imgs,
      Login,
      Search,
      SortPage,
      Videos,
      PersonInfo,
      UpdatePersonInfo,
      about
    },
    created () {
      this.$nextTick(() =>{
        var qs = require('qs')
        var self = this

        /*导航*/
        this.$http.post(this.PRIFIX + '/api/homePage/android/nav').then(function (response) {
          response = response.data
          if (response.code === 0) {
            saveNav(response.data.indexNavigation)
            self.navigation = loadNav()
            self.navNum = loadNavCss()
          } else {
            self.$Notice.error({
              title: '内容获取失败',
            })
          }
        }).catch(function (error) {})

        /*轮播图*/
        this.$http.post(this.PRIFIX + '/api/homePage/slide').then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.slide = response.data.slideContent
          } else {
            self.$Notice.error({
              title: '内容获取失败',
            })
          }
        }).catch(function (error) {})

        /*推荐*/
        this.$http.post(this.PRIFIX + '/api/homePage/recommend').then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.recommendList = response.data
          } else {
            self.$Notice.error({
              title: '内容获取失败',
            })
          }
        }).catch(function (error) {})

        /*推送文章*/
        this.$http.post(this.PRIFIX + '/api/homePage/random/article',qs.stringify({
          'page': 1,
          'size': 10
        })).then(function (response) {
          response = response.data
          if (response.code === 0) {
            self.articleRecommendAgen = response.data.article.pageContent
          } else {
            self.$Notice.error({
              title: '内容获取失败',
            })
          }
        }).catch(function (error) {})
      })
    }
  }

</script>

<style lang="less" scoped>
  .index {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
  }

</style>



