<template>
  <div>
    <Menu class="navWrapper" mode="horizontal" active-name="1">
      <MenuItem name="0" class="clearBorderBottom">
        <img class="logo" src="../../assets/img/logo.jpg">
      </MenuItem>
      <Breadcrumb class="Bspan">
        <BreadcrumbItem to="/">首页</BreadcrumbItem>
        <BreadcrumbItem to="/components/breadcrumb">娱乐</BreadcrumbItem>
        <BreadcrumbItem>正文</BreadcrumbItem>
      </Breadcrumb>
      <MenuItem name="9" class="clearBorderBottom search">
        <AutoComplete icon="search" :filter-method="filterMethod" placeholder="搜索"
                      style="width:200px">
        </AutoComplete>
      </MenuItem>
    </Menu>
  </div>
</template>

<script type="text/ecmascript-6">
  export default{
    methods: {
      /*数据源*/
      hotSearch() {
        const array = []
        this.data3.forEach((search, i) => {
          i++
          search = i + " " + search
          array.push(search)
        })
        return array
      },
      /*过滤*/
      filterMethod (value, option) {
        return option.toUpperCase().indexOf(value.toUpperCase()) !== -1;
      }
    }
  }
</script>

<style lang="less" scoped>
  .navWrapper {
    position: fixed;
    top: 0;
    left: 0;
    background: #fff !important;
    width: 100%;
    height: 60px;
    -moz-box-shadow: 0px 0px 5px #999; /* 老的 Firefox */
    box-shadow: 0px 0px 5px #999;
  }

  .logo {
    width: 108px;
    height: 27px;
    margin-top: 15px;
    margin-left: 108px;
  }

  .clearBorderBottom {
    border-bottom: none !important;
  }

  .search {
    float: right !important;
    right: 108px;
  }

  .Bspan {
    display: inline-block;
    font-size: 18px;
  }

</style>



